r\n    top: 50px;\r\n    left: -180px;\r\n    width: 150px;\r\n    transition: all 0.3s;\r\n    padding: 8px;\r\n    background-color: white;\r\n    overflow: hidden;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation.open {\r\n    left: 0;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    text-decoration: none;\r\n    color: black;\r\n    padding: 10px;\r\n    margin-bottom: 5px;\r\n    width: 100%;\r\n  }\r\n\r\n/* hero */\r\n.skip-content {\r\n  position: absolute;\r\n  top: -40px;\r\n  left: 0;\r\n  background-color: #bf1722;\r\n  color: white;\r\n  padding: 8px;\r\n  z-index: 100;\r\n}\r\n \r\n.skip-content:focus {\r\n   top: 0;\r\n}\r\n   \r\n  .content {\r\n    margin: 0 auto;\r\n    min-height: 100%;\r\n  }\r\n   \r\n  .content .content__heading {\r\n    font-weight: normal;\r\n  }\r\n   \r\n  .hero{\r\n    background: url(../public/images/heros/hero-image_2.jpg);\r\n    background-size:cover;\r\n    background-position: center;\r\n    min-height: 80vh;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n    text-align:center;\r\n    color: white;\r\n    margin-bottom: 5em;\r\n  }\r\n  \r\n  .hero-item{\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    text-align: center;\r\n  }\r\n  .hero-item > h2{\r\n    font-family: 'Montserrat';\r\n    font-weight: bold;\r\n    font-size: 3rem;\r\n    width: auto;\r\n    margin-bottom: 20px;\r\n  }\r\n  .hero-item > p{\r\n    font-family: 'Montserrat';\r\n    font-size: 1rem;\r\n    margin-bottom: 2px;\r\n    padding-bottom: 2px;\r\n  }\r\n\r\n/* resto */\r\n.header-text{\r\n    text-align: center;\r\n    font-weight: bold;\r\n}\r\n.restos{\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 16px;\r\n    margin: 32px 0;\r\n}\r\n/* review */\r\n.review-container{\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 20px;\r\n  padding: 2em;\r\n  margin: 1em;\r\n  align-items: center;\r\n}\r\n.review-item{\r\n  background-color: rgb(202, 202, 202);\r\n  /* border: solid rgb(255, 123, 0); */\r\n  border-radius: 15px;\r\n  height: 300px;\r\n  box-shadow: 6px 5px 9px 0px rgba(0,0,0,0.75);\r\n}\r\n.review-item__icon{\r\n  border-radius: 15px 15px 0px 0px;\r\n  font-size: 32px;\r\n  text-align: center;\r\n  background-color: white;\r\n  border: solid rgb(255, 123, 0);\r\n}\r\n.review-item__detail{\r\n  margin: 0px 20px 20px 20px;\r\n}\r\n\r\n.review-item__detail > h3{\r\n  text-align: center;\r\n}\r\n.review-review{\r\n  color: white;\r\n}\r\n\r\n.review-date{\r\n  font-style: italic;\r\n  font-size: 12px;\r\n  text-align: center;\r\n  margin-bottom: 1em;\r\n}\r\n\r\n/* like  */\r\n.like-btn{\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: rgb(255, 123, 0);\r\n  \r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n  \r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n/* fav */\r\n\r\n\r\n/* item resto */\r\n.resto-item{\r\n    width: 100%;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n    border-radius: 8px;\r\n    overflow: hidden;\r\n}\r\n.resto-item__header {\r\n    position: relative;\r\n  }\r\n.resto-item__content-name a{\r\n  font-size: 24px;\r\n  padding-bottom: 0.5em;\r\n\r\n}\r\n  .resto-item .resto-item__header .resto-item__header__poster {\r\n    width: 100%;\r\n    height: 300px;\r\n  }\r\n   \r\n  .resto-item .resto-item__header .resto-item__header__rating {\r\n    position: absolute;\r\n    padding: 8px;\r\n    bottom: 20px;\r\n    left: 0;\r\n    display: inline-block;\r\n    background-color: black;\r\n    color: white;\r\n  }\r\n   \r\n  .resto-item .resto-item__header .resto-item__header__rating .resto-item__header__rating__score {\r\n    margin-left: 10px;\r\n  }\r\n   \r\n  .resto-item .resto-item__content {\r\n    padding: 16px;\r\n  }\r\n  .resto-item .resto-item__content p {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 3;\r\n    -webkit-box-orient: vertical;\r\n  }\r\n\r\n  /* detail */\r\n  .detail-container{\r\n    display: grid;\r\n    grid-template-columns: repeat(1,1fr);\r\n    padding: 0.5em;\r\n    margin: 0.5em;\r\n  }\r\n\r\n  .detail__poster{\r\n    width:auto;\r\n    height: 200px;\r\n  }\r\n\r\n  .detail-desc{\r\n    margin-top: 2em;\r\n  }\r\n\r\n  .detail-desc__name{\r\n    margin-bottom: 10px;\r\n    color: rgb(255, 174, 0);\r\n  }\r\n\r\n  .detail-desc__rating{\r\n    width: fit-content;\r\n    padding: 4px;\r\n    bottom: 10px;\r\n    display: inline-block;\r\n    color: rgb(255, 255, 255);\r\n    background-color:rgba(0, 0, 0, 0.694) ;\r\n  }\r\n  .detail-desc__desc{\r\n    font-family: 'montserrat',;\r\n  }\r\n  \r\n  .menu{\r\n    display:grid;\r\n    grid-template-columns: repeat(1,1fr);\r\n    padding: 20px;\r\n    margin: 15px;\r\n  }\r\n  .menu-list{\r\n    list-style: none;\r\n    margin: 15px;\r\n    border-radius: 20px;\r\n    border: dotted rgb(255, 106, 0);\r\n  }\r\n\r\n  .food-list > li{\r\n    margin-left: 20px;\r\n  }\r\n  .drink-list > li{\r\n    margin-left: 20px;\r\n  }\r\n  .food-list > h2{\r\n    color: rgb(255, 225, 0);\r\n    text-align: center;\r\n    font-weight: bold;\r\n    font-size: 32px;\r\n\r\n  }\r\n  .drink-list > h2{\r\n    color: rgb(255, 225, 0);\r\n    text-align: center;\r\n    font-weight: bold;\r\n    font-size: 32px;\r\n\r\n  }\r\n  /* footer */\r\n  footer{\r\n    color: white;\r\n    background-color:rgb(255, 123, 0) ;\r\n    text-align: center;\r\n    font-weight: bold;\r\n    font-size: large;\r\n    margin-top: 30px;\r\n  }\r\n  \r\n\r\n\r\n  /* responsive */\r\n  @media screen and (min-width:650px) {\r\n    .app-bar {\r\n        grid-template-columns: 1fr auto;\r\n        padding: 8px 32px;\r\n      }\r\n     \r\n      .app-bar .app-bar__brand h1 {\r\n        font-size: 1.5em;\r\n      }\r\n     \r\n      .app-bar .app-bar__menu {\r\n        display: none;\r\n      }\r\n     \r\n      .app-bar .app-bar__navigation {\r\n        position: static;\r\n        width: 100%;\r\n      }\r\n     \r\n      .app-bar .app-bar__navigation ul li {\r\n        display: inline-block;\r\n      }\r\n     \r\n      .app-bar .app-bar__navigation ul li a {\r\n        display: inline-block;\r\n        width: 120px;\r\n        text-align: center;\r\n        margin: 0;\r\n      }\r\n      .review-container{\r\n        display: grid;\r\n        grid-template-columns: repeat(3, 1fr);\r\n      }\r\n  }\r\n\r\n  @media screen and (min-width: 800px) {\r\n    .app-bar .app-bar__brand h1 {\r\n      font-size: 2em;\r\n    }\r\n    .restos {\r\n        grid-template-columns: repeat(3, 1fr);\r\n        padding:10px;\r\n      }\r\n    .detail-container{\r\n      grid-template-columns: repeat(2, 1fr);\r\n      gap: 20px;\r\n    }\r\n    .detail__poster{\r\n      margin-top: 40px;\r\n      width: auto;\r\n      height: 300px;\r\n    }\r\n    .detail-page{\r\n      display: grid;\r\n      padding: 20px;\r\n      margin: 30px;\r\n    }\r\n    .menu{\r\n      display: grid;\r\n      grid-template-columns: repeat(2,1fr);\r\n    }\r\n    \r\n}\r\n@media screen and (max-width:320px){\r\n  .detail-container{\r\n    display: flex;\r\n    flex-direction: column;\r\n    \r\n  }\r\n  \r\n}"],"sourceRoot":""}]);
